import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { PolicyInformationComponent } from './policy-information/policy-information.component';
import { NamedInsuredComponent } from './named-insured/named-insured.component';
import { ValidatorsService } from './policy-information/validators.service';

import { routing } from './app.routes';

@NgModule({
  declarations: [
    AppComponent,
    PolicyInformationComponent,
    NamedInsuredComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
	routing
  ],
  providers: [ValidatorsService],
  bootstrap: [AppComponent]
})
export class AppModule { }



