import { Routes, RouterModule } from '@angular/router';
import { PolicyInformationComponent } from './policy-information/policy-information.component';
import { NamedInsuredComponent } from './named-insured/named-insured.component';

const APP_ROUTES: Routes = [
    {path: 'policyInformation', component: PolicyInformationComponent},
    {path: 'namedInsured', component: NamedInsuredComponent}
]

export const routing = RouterModule.forRoot(APP_ROUTES);


